<template>
	<div id="withdraw">
		 <div class="tit">申请提现</div>
		 <div class="yue_box">
			 <div><img src="/static/icon/8.png"><span>账户余额</span><div>{{user.balance_data.balance}}</div></div>
			 <div><img src="/static/icon/9.png"><span>冻结金额</span><div>{{cashmoney}}</div></div></div>
	     <div class="txz">
	       <div class="tite">请选择提款账户</div>
		     <div class="select_box" style="margin-top:10px;margin-left:24px;">
		     <img src="/static/icon/10.png" style="width: 13px; margin-top: -7px; z-index: 10;">
		     <Select style="width:413px;" placeholder="请选择提款账户" v-model="bank_id">
			        <Option v-for="item in bank_list" :value="item.Id" :key="item.Id">{{item.name}} - {{item.bank_type}}</Option>
			 </Select>
			 </div>
	     </div>
	     <div class="shenq_box" v-if="user.safety == 1">
			 <div class="tite">请输入谷歌验证码</div>
			 <div class="settle_money_box settle_money_box_x">
			 <input type="number" v-model="safety_code" placeholder="输入谷歌验证码">
			 </div>
		 </div>
		 <div class="shenq_box">
			 <div class="tite">申请提现（提现金额只能为整数）</div>
			 <div class="settle_money_box" :class="{'settle_money_box_w':setbox,'settle_money_box_x':!setbox}">
			 <input type="text" v-model="money" placeholder="输入提现金额(数字)" v-on:input="oninput($event)">
			 </div>
			 <span>(最低提现金额:{{Explain.cash_min}}元,最高提现金额:{{Explain.cash_max}})</span>
			 <div class="settle_ntn" :class="{'settle_ntn_w':setbox,'settle_ntn_x':!setbox}" @click="getwithdraw()">提现</div>
		 </div>
  	</div>
</template>
<style>
#withdraw .txz{
	padding-top: 20px;
}
#withdraw .txz .tite {
    margin-left: 32px;
    font-weight: 700;
    color: #68728c;
    font-size: 14px;
}
</style>
<script>
    export default {
	    data () {
        return {
            user:"",
            bank_id:'',
            cashmoney:'0.00',
            bank_list:'',
            money:'',
            setbox:true,
            safety_code:'',
            Explain:""
        }
	    },
	    mounted(){
			this.getinfo();
		    this.ajaxlist();
      },
	    methods: {
			getinfo(){
			  if(localStorage.getItem('user_info')){
			    var user_info = JSON.parse(localStorage.getItem('user_info'));
			    if(user_info.time < new Date().getTime()){
			      this.ajaxuserinfo();
			    }else{
			      this.user = user_info.data;
			    }
			  }else{
			    this.ajaxuserinfo();
			  }
			},
			ajaxuserinfo(){
			  this.$http.get('api/user/userinfo',{}, res => {
			       if (res.status == 200) {
			           if(res.data.code == 200){
			             var userinfo = {};
			                 userinfo.time = new Date().getTime() + 10000;
			                 userinfo.data = res.data.data;
			             localStorage.setItem('user_info',JSON.stringify(userinfo));
			             this.user = userinfo.data;
			           }else if(res.data.code == 201){
			             this.$Message.warning(res.data.msg);
			           }
			       } else {
			          console.log(res.message);
			       }
			  });
			},
	       oninput(e){
	          if(this.money == ""){
	             this.setbox=true;
	          }else{
	             this.setbox=false;
	          }
	       },
	       ajaxwithdraw(){
            this.$http.post('api/user/withdraw',{code:101,cash_account_id:this.bank_id,money:this.money,safety_code:this.safety_code}, res => {
                 if (res.status == 200) {
                     if(res.data.code == 200){
                           this.user.balance_data.balance = (this.user.balance_data.balance - this.money).toFixed(2);
                         var userinfo = {};
                             userinfo.time = new Date().getTime() + 10000;
                             userinfo.data = this.user;
                       localStorage.setItem('user_info',JSON.stringify(userinfo));
                       this.$Message.success(res.data.msg);
                     }else if(res.data.code == 201){
                       this.$Message.warning(res.data.msg);
                     }
                 } else {
                    console.log(res.message);
                 }
            });
	       },
         getwithdraw(){
           if(this.bank_id == ""){
             this.$Message.warning("请选择提现银行卡");
           }else if(parseFloat(this.money) > this.Explain.cash_max){
             this.$Message.warning("当前最高可提现"+this.Explain.cash_max+"元");
           }else if(parseFloat(this.money) < this.Explain.cash_min){
             this.$Message.warning("当前最低可提现"+this.Explain.cash_min+"元");
           }else if(parseFloat(this.money) > parseFloat(this.user.balance_data.balance)){
             this.$Message.warning("当前余额不足");
           }else if(this.user.safety == 1 && this.safety_code.length < 5){
             this.$Message.warning("请输入正确的谷歌验证码");
           }else{
             this.ajaxwithdraw();
           }
         },
	       ajaxlist(){
            this.$http.post('api/user/withdraw',{code:100}, res => {
                 if (res.status == 200) {
                     if(res.data.code == 200){
                       this.bank_list = res.data.data.bank_list;
                       this.Explain = res.data.data.Explain;
                     }else if(res.data.code == 201){
                       this.$Message.warning(res.data.msg);
                     }
                 } else {
                    console.log(res.message);
                 }
            });
	       }
	    }
    }
</script>



